import React from 'react';
import { render, waitFor, fireEvent } from '@testing-library/react-native';
import { Dimensions } from 'react-native';

// Screen to test
import VocabularyRevision from '../../../src/screens/VocabularyRevision';

// Mock custom hooks
const mockMarkRevisionCompleted = jest.fn();
jest.mock('../../../src/hooks/useRevisionManager', () => ({
  __esModule: true,
  default: jest.fn(() => ({
    markRevisionCompleted: mockMarkRevisionCompleted,
  })),
}));

const mockGoBack = jest.fn(); // Moved to top level
jest.mock('@react-navigation/native', () => ({
  useNavigation: jest.fn(() => ({
    goBack: mockGoBack,
  })),
}));

jest.mock('../../../src/hooks/useRevisionData', () => ({
  __esModule: true,
  default: jest.fn(() => ({
    revisionQuestions: [],
    isLoading: false,
    error: null,
    stats: { totalLearned: 0 },
    hasEnoughWords: false,
    canGenerateQuestions: false,
  })),
}));

jest.mock('../../../src/hooks/useQuizEngine', () => ({
  __esModule: true,
  default: jest.fn(() => ({
    currentQuestion: null,
    currentQuestionIndex: 0,
    totalQuestions: 0,
    score: 0,
    isFinished: false,
    showResult: false,
    handleAnswer: jest.fn(() => true),
    goToNextQuestion: jest.fn(),
    handleRestart: jest.fn(),
  })),
}));

// Mock ThemeContext
jest.mock('../../../src/contexts/ThemeContext', () => ({
  ThemeContext: {
    Consumer: ({ children }) => children({
      colors: {
        background: "#F8FAFC",
        surface: "#FFFFFF",
        text: "#1F2937",
        textSecondary: "#6B7280",
        primary: "#3B82F6"
      }
    }),
    useContext: () => ({
      colors: {
        background: "#F8FAFC",
        surface: "#FFFFFF",
        text: "#1F2937",
        textSecondary: "#6B7280",
        primary: "#3B82F6"
      }
    }),
  },
}));

// Mock Dimensions for Animated values
jest.spyOn(Dimensions, 'get').mockReturnValue({ width: 300, height: 600 });

describe('VocabularyRevision Integration Tests', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    jest.useFakeTimers(); // Use fake timers
    // Reset mock implementations for each test
    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      revisionQuestions: [],
      isLoading: false,
      error: null,
      stats: { totalLearned: 0 },
      hasEnoughWords: false,
      canGenerateQuestions: false,
    }));
    require('../../../src/hooks/useQuizEngine').default.mockImplementation(() => ({
      currentQuestion: null,
      currentQuestionIndex: 0,
      totalQuestions: 0,
      score: 0,
      isFinished: false,
      showResult: false,
      handleAnswer: jest.fn(() => true),
      goToNextQuestion: jest.fn(),
      handleRestart: jest.fn(),
    }));
    mockMarkRevisionCompleted.mockClear();
    mockGoBack.mockClear(); // Clear mock for each test
  });

  afterEach(() => {
    jest.clearAllTimers(); // Clear all timers after each test
  });

  test('renders loading state initially', async () => {
    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      isLoading: true,
      revisionQuestions: [],
      error: null,
      stats: { totalLearned: 0 },
      hasEnoughWords: false,
      canGenerateQuestions: false,
    }));

    const { getByText } = render(<VocabularyRevision />);
    await waitFor(() => {
      expect(getByText('Récupération de vos mots appris')).toBeTruthy();
    });
  });

  test('renders error state', async () => {
    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      isLoading: false,
      error: 'Failed to load data',
      revisionQuestions: [],
      stats: { totalLearned: 0 },
      hasEnoughWords: false,
      canGenerateQuestions: false,
    }));

    const { getByText } = render(<VocabularyRevision />);
    await waitFor(() => {
      expect(getByText('Failed to load data')).toBeTruthy();
    });
  });

  test('renders locked state when not enough words', async () => {
    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      isLoading: false,
      error: null,
      revisionQuestions: [],
      stats: { totalLearned: 30 }, // Less than MIN_WORDS_FOR_REVISION_UNLOCK (50)
      hasEnoughWords: false,
      canGenerateQuestions: false,
    }));

    const { getByText } = render(<VocabularyRevision />);
    await waitFor(() => {
      // Corrigé: utilise le texte réel affiché dans QuickActions
      expect(getByText(/Apprenez 50 mots pour débloquer le mode révision et tester vos connaissances !/i)).toBeTruthy();
      expect(getByText('30 / 50')).toBeTruthy();
    });
  });

  test('renders noWords state when enough words but cannot generate questions', async () => {
    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      isLoading: false,
      error: null,
      revisionQuestions: [],
      stats: { totalLearned: 60 }, // More than MIN_WORDS_FOR_REVISION_UNLOCK
      hasEnoughWords: true,
      canGenerateQuestions: false,
    }));

    const { getByText } = render(<VocabularyRevision route={{ params: { questionsCount: 10 } }} />);
    await waitFor(() => {
      expect(getByText(/Continuez d'apprendre pour diversifier les questions !/i)).toBeTruthy();
      expect(getByText(/Un quiz de 10 questions ne peut pas être généré pour ce niveau./i)).toBeTruthy();
    });
  });

  test('renders QuizScreen and handles answer and continue', async () => {
    const mockQuestions = [
      { 
        id: 'q1', 
        word: 'Word 1', 
        choices: ['A', 'B', 'C'], // Corrigé: choices au lieu d'options
        correctAnswer: 'A' 
      },
      { 
        id: 'q2', 
        word: 'Word 2', 
        choices: ['D', 'E', 'F'], // Corrigé: choices au lieu d'options
        correctAnswer: 'D' 
      },
    ];

    const useRevisionDataMock = require('../../../src/hooks/useRevisionData').default;
    useRevisionDataMock.mockImplementation(() => ({
      revisionQuestions: mockQuestions,
      isLoading: false,
      error: null,
      stats: { totalLearned: 100 },
      hasEnoughWords: true,
      canGenerateQuestions: true,
    }));

    const useQuizEngineMock = require('../../../src/hooks/useQuizEngine').default;
    const mockGoToNextQuestion = jest.fn();
    // Mock useQuizEngine to return different states
    useQuizEngineMock
      .mockImplementationOnce(() => ({ // Initial state
        currentQuestion: mockQuestions[0],
        currentQuestionIndex: 0,
        totalQuestions: mockQuestions.length,
        score: 0,
        isFinished: false,
        showResult: false,
        handleAnswer: jest.fn(() => true),
        goToNextQuestion: mockGoToNextQuestion,
        handleRestart: jest.fn(),
      }))
      .mockImplementationOnce(() => ({ // After answering
        currentQuestion: mockQuestions[0],
        currentQuestionIndex: 0,
        totalQuestions: mockQuestions.length,
        score: 1, // Assuming correct answer
        isFinished: false,
        showResult: true, // Show result after answer
        handleAnswer: jest.fn(() => true),
        goToNextQuestion: mockGoToNextQuestion,
        handleRestart: jest.fn(),
      }));


    const { getByText, queryByText } = render(<VocabularyRevision />);

    // Initial QuizScreen state
    await waitFor(() => {
      expect(getByText('Word 1')).toBeTruthy();
      expect(queryByText('Result Screen')).toBeNull();
    });

    // Simulate answering correctly
    fireEvent.press(getByText('A'));
    jest.runOnlyPendingTimers(); // Run timers for state update

    // Simulate continue to next question
    await waitFor(() => { // Wait for "Continuer" to appear
      expect(getByText('Continuer')).toBeTruthy();
    });
    fireEvent.press(getByText('Continuer'));
    expect(mockGoToNextQuestion).toHaveBeenCalled();
  });

  test('renders ResultScreen when quiz is finished', async () => {
    const mockQuestions = [
      { 
        id: 'q1', 
        word: 'Word 1', 
        choices: ['A', 'B', 'C'], // Corrigé: choices au lieu d'options
        correctAnswer: 'A' 
      },
    ];

    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      revisionQuestions: mockQuestions,
      isLoading: false,
      error: null,
      stats: { totalLearned: 100 },
      hasEnoughWords: true,
      canGenerateQuestions: true,
    }));

    const useQuizEngineMock = require('../../../src/hooks/useQuizEngine').default;
    useQuizEngineMock.mockImplementation(() => ({
      currentQuestion: null,
      currentQuestionIndex: 1,
      totalQuestions: mockQuestions.length,
      score: 1, // Ensure score is 1 for this test
      isFinished: true,
      showResult: true,
      handleAnswer: jest.fn(),
      goToNextQuestion: jest.fn(),
      handleRestart: jest.fn(),
    }));

    const { getByText } = render(<VocabularyRevision />);

    await waitFor(() => {
      jest.runOnlyPendingTimers(); // Run timers for animation
      // Corrigé: cherche les éléments qui existent réellement
      expect(getByText('1')).toBeTruthy(); // For the score
      expect(getByText('/1')).toBeTruthy(); // For the total questions
      expect(getByText('Rejouer')).toBeTruthy(); // Corrigé: "Rejouer" au lieu de "Recommencer"
      expect(getByText('Terminer')).toBeTruthy();
    }, { timeout: 1500 }); // Increase timeout to allow animation to complete
  });

  test('handles restart from ResultScreen', async () => {
    const mockQuestions = [
      { 
        id: 'q1', 
        word: 'Word 1', 
        choices: ['A', 'B', 'C'], // Corrigé: choices au lieu d'options
        correctAnswer: 'A' 
      },
    ];

    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      revisionQuestions: mockQuestions,
      isLoading: false,
      error: null,
      stats: { totalLearned: 100 },
      hasEnoughWords: true,
      canGenerateQuestions: true,
    }));

    const useQuizEngineMock = require('../../../src/hooks/useQuizEngine').default;
    const mockHandleRestart = jest.fn();
    useQuizEngineMock.mockImplementation(() => ({
      currentQuestion: null,
      currentQuestionIndex: 1,
      totalQuestions: mockQuestions.length,
      score: 1,
      isFinished: true,
      showResult: true,
      handleAnswer: jest.fn(),
      goToNextQuestion: jest.fn(),
      handleRestart: mockHandleRestart,
    }));

    const { getByText } = render(<VocabularyRevision />);

    await waitFor(() => {
      expect(getByText('Rejouer')).toBeTruthy(); // Corrigé: "Rejouer" au lieu de "Recommencer"
    });

    fireEvent.press(getByText('Rejouer'));
    expect(mockHandleRestart).toHaveBeenCalled();
  });

  test('handles finish from ResultScreen', async () => {
    const mockQuestions = [
      { 
        id: 'q1', 
        word: 'Word 1', 
        choices: ['A', 'B', 'C'], // Corrigé: choices au lieu d'options
        correctAnswer: 'A' 
      },
    ];

    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      revisionQuestions: mockQuestions,
      isLoading: false,
      error: null,
      stats: { totalLearned: 100 },
      hasEnoughWords: true,
      canGenerateQuestions: true,
    }));

    const useQuizEngineMock = require('../../../src/hooks/useQuizEngine').default;
    useQuizEngineMock.mockImplementation(() => ({
      currentQuestion: null,
      currentQuestionIndex: 1,
      totalQuestions: mockQuestions.length,
      score: 1,
      isFinished: true,
      showResult: true,
      handleAnswer: jest.fn(),
      goToNextQuestion: jest.fn(),
      handleRestart: jest.fn(),
    }));

    const { getByText } = render(<VocabularyRevision />);

    await waitFor(() => {
      expect(getByText('Terminer')).toBeTruthy();
    });

    fireEvent.press(getByText('Terminer'));
    await waitFor(() => {
      expect(mockMarkRevisionCompleted).toHaveBeenCalledWith(mockQuestions, 1, 1);
      expect(mockGoBack).toHaveBeenCalled();
    });
  });

  test('handleGoBack is called when back button is pressed in QuizScreen', async () => {
    const mockQuestions = [
      { 
        id: 'q1', 
        word: 'Word 1', 
        choices: ['A', 'B', 'C'], // Corrigé: choices au lieu d'options
        correctAnswer: 'A' 
      },
    ];

    require('../../../src/hooks/useRevisionData').default.mockImplementation(() => ({
      revisionQuestions: mockQuestions,
      isLoading: false,
      error: null,
      stats: { totalLearned: 100 },
      hasEnoughWords: true,
      canGenerateQuestions: true,
    }));

    const useQuizEngineMock = require('../../../src/hooks/useQuizEngine').default;
    useQuizEngineMock.mockImplementation(() => ({
      currentQuestion: mockQuestions[0],
      currentQuestionIndex: 0,
      totalQuestions: mockQuestions.length,
      score: 0,
      isFinished: false,
      showResult: false,
      handleAnswer: jest.fn(),
      goToNextQuestion: jest.fn(),
      handleRestart: jest.fn(),
    }));

    const { getByLabelText } = render(<VocabularyRevision />); // Changed to getByLabelText

    await waitFor(() => {
      expect(getByLabelText('Retour')).toBeTruthy(); // Changed to getByLabelText
    });

    fireEvent.press(getByLabelText('Retour')); // Changed to getByLabelText
    expect(mockGoBack).toHaveBeenCalled();
  });
});